import Router from 'vue-router'
import Vue from 'vue'
import routes from './routes'
import { Toast } from 'vant'

Vue.use(Router)
const router = new Router({
  routes
})

/* 路由开始生命周期 */
router.beforeEach((to, from, next) => {
  // 创建加载器
  
  if (to.meta.requiresAuth) {
    const token = localStorage.getItem('access_token')
    // 如果有验证信息,就放行
    if (token) {
    //   Toast.loading({
    //     message: '正在加载界面...',
    //     forbidClick: true
    //   })
      next(); //放行
    } else {//打回到登录页面
      next('/login'); //放行
    }
  } else {
    // 不需要验证,直接放行
    next()
  }
  // 如果不存在token 则跳转到登录页
  // if (!sessionStorage.token && to.path !== '/login') {
  //   Toast.error('请先登录')
  //   next({
  //     path: '/login'
  //   })
  // } else {
  //   next()
  // } 
})

/* 路由结束生命周期 */
router.afterEach(() => {
  Toast.clear()
  window.scrollTo(0, 0)
})

export default router
