<template>
  <div id="app" class="size">
    <router-view />
  </div>
</template>

<script>
// import Index from './pages/Index'
export default {
  name: "App",
  created() {
    this.updateHtmlTitle();
  },
  watch: {
    $route() {
      this.updateHtmlTitle();
    }
  },
  methods: {
    /**
     * @description: 设置HTml title
     * @date 2020/2/11
     */
    updateHtmlTitle() {
      document.title = this.$route.meta.title || "牦牛可追溯系统";
      // console.log(this.$route.meta.title);
    }
  }
};
</script>

<style lang="less">
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>
